.btn {
  box-sizing: border-box;
  padding: 20px;
  border: none;
  background: transparent;
  font-size: 15px; }
  .btn_success {
    background: turquoise;
    color: #fff; }
  .btn_error {
    background: red;
    color: #fff; }
  .btn_default {
    background: grey;
    color: #494949; }
  .btn_large {
    padding: 30px;
    font-size: 20px;
    font-weight: bold; }
